import React, { Component } from 'react';
import './index.css';
import 'antd/dist/antd.css';
import './index.css';
import { Layout } from 'antd';
import { Input, Select } from 'antd';
import { Modal } from 'antd';
import axios from 'axios';

const { Option } = Select;
const { Content } = Layout;
class NameForm extends Component {
    constructor() {
        super();
        this.state = {
            loading: false,
            response: null,
            value: ''
        };
    }

    selectAfter = () => {
        return (
            <Select placeholder="Select Email Ending" style={{ width: 200 }} onChange={(value) => this.onSelectChange(value)}>
                <Option value="@mediamarktsaturn.com">@mediamarktsaturn.com</Option>
                <Option value="@mediamarktsaturn.de">@mediamarktsaturn.de</Option>
            </Select>
        )
    }

    onSelectChange = (value) => {
        this.setState({
            selectedOption: value
        })
        console.log(value)
    }


    submit = async (e) => {
        e.preventDefault();
        this.setState({ ...this.state, loading: true })
        const data = {
            "project_id": this.state.valueProjectID,
            "role": this.state.valueRole,
            "e_mail": this.state.valueEmail + this.state.selectedOption
        };
        //  http://127.0.0.1:5000/add_user_to_binding
        console.log(data)
        fetch('https://auto-iam-api-bg4m6eql4q-ew.a.run.app', {
            method: 'POST',
            headers: {
                'Content-type': 'application/json',
            },
            body: JSON.stringify(data)
        })
            .then(res => res.json())
            .then(res => console.log(res));
        this.setState({
            ...this.state,
            loading: false,
        })
        Modal.info({
            title: "The user has been assigned to the role"
        })
    }

    check = async () => {
        try {
            const access_token = localStorage.getItem('access_token')
            const response = await axios.get(`https://cloudresourcemanager.googleapis.com/v1/projects/` + this.state.valueProjectID, {
                headers: {
                    'Authorization': `Bearer ${access_token}`
                }
            });
            console.log(response);
            if (!response.data.projectId) {
                Modal.error({
                    title: 'This Project ID is not correct',
                    content: 'Please look if the spelling is right.',
                });
            }
            else{
                Modal.info({
                    title: "Project exist!"
                })
            }
        } catch (error) {
            console.error(error);
            Modal.error({
                title: 'Api is not reachable ',
                content: 'This is the ' + error,
            });
        }
    }

    handleChangeProjectID = (event) => {
        console.log(event.target.value)
        this.setState({
            ...this.state,
            valueProjectID: event.target.value
        })
    }
    handleChangeRole = (event) => {
        console.log(event.target.value)
        this.setState({
            ...this.state,
            valueRole: event.target.value
        })
    }
    handleChangeEmail = (event) => {
        console.log(event.target.value)
        this.setState({
            ...this.state,
            valueEmail: event.target.value
        })
    }


    render() {
        return (
            <Layout>
                <Content style={{ padding: '0 50px', textAlign: 'left' }}>
                    <div className="App">
                        <header className="App-header">
                            <form onSubmit={this.submit}>
                                <br />
                                Project-ID :
              <Input required placeholder="input in small letters" type="text" value={this.state.valueProjectID} onChange={this.handleChangeProjectID} />
                                <br /><br />
                                <label>
                                    Role :
              <Input required placeholder="first letter in upper case" type="text" value={this.state.valueRole} onChange={this.handleChangeRole} />
                                </label>
                                <br /> <br />
                                <label>
                                    E-Mail :
              <Input required placeholder="input in small letters" addonAfter={this.selectAfter()} type="text" value={this.state.valueEmail} onChange={this.handleChangeEmail} />
                                </label>
                                <br />
                                <br />
                                <button type="submit"> Submit </button>
                            </form>
                            <form>
                                <br/>
                                <button onClick={this.check} type="button"> Check Project-ID </button>
                            </form>
                        </header>
                    </div>
                </Content>

            </Layout>
        );
    }
}

export default NameForm;